//To check count and calculating real price of product

import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';

import { Button } from 'react-bootstrap';

import Item from './Item';
import Counter from './Counter'

const ItemDetailContainer = ({setCart}) =>{

    
    const[product, setProduct] = useState([]);
    const[price, setPrice] = useState();
   
    const addProduct = (e) => {
        setCart(product);
    }

    let {id} = useParams();
    id = id.replace(/:/g, '');
    
    
    useEffect(() => {
        if(id){
            fetch(`https://api.mercadolibre.com//items?ids=${id}`)
            .then(response => response.json())
            .then(data => {
                let [info] = data;
                

                setProduct(info.body);

            });
            
            
            document.title = `${product.title}`;
        }
        }, [id, product.title])

    return(
        <div className='container-flex-center'>
            <Item {...product} basePrice = {price}/>
            <Counter price = {product.price} setPrice = {setPrice}/>
            <Button variant='primary' onClick={addProduct}>Agregar al carrito</Button>
        </div>
    )
}

export default ItemDetailContainer;