import {useState, useEffect} from 'react';
import { useParams, Link } from 'react-router-dom';

import Item from './Item';


//Camaras MLM1039
//Celulares  MLM1051
//Computadoras  MLM1648
//Videojuegos MLM1144

const CategoryListContainer = () =>{
    
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [title, setTitle] = useState('');
    
    let {id} = useParams();
    id = id.replace(/:/g, '');
    
    useEffect(() => {
        setLoading(true);
        fetch(`https://api.mercadolibre.com/sites/MLM/search?category=${id}`)
        .then(response => response.json())
        .then(data => {
            document.title = `${data.filters[0].values[0].name}`;
            setTitle(data.filters[0].values[0].name);
            setProducts(data.results)})
            .finally(() => {
                
                setLoading(false)

            });
        }, [id]);
    
    if(loading){
        return <h1 key={"01"}>Cargando...</h1>
    }
    else if(!loading){
        return(
            <div className='container-flex-center' key={id}>
                <h1 key={id}>{title}</h1>
                <ul className="item-list-container">
                    {products.map((product) => <Link key={product.key} to={`/house_tech_project_ecommerce/detail/:${product.id}`}><Item key={product.key} {...product} /></Link>)}
                </ul> 
            </div>
        )
    }
}

export default CategoryListContainer;
