import './App.scss';

import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { useState } from 'react';

import ItemListContainer from './components/ItemListContainer';
import ItemDetailContainer from './components/ItemDetailContainer';
import CustomNavbar from './components/Navbar';
import CategoryListContainer from './components/CategoryListContainer';


function App() {
const [cart, setCart] = useState([]);

  const addProduct = (product) => {
    setCart([...cart, product]);
  }


  return(
    <div className="App">
      <BrowserRouter >
        <CustomNavbar cart = {cart}/>

        <Routes>
          <Route path='/house_tech_project_ecommerce/' element={<ItemListContainer setCart={addProduct} title="Nuestros Productos"/>}/>
          <Route path='/house_tech_project_ecommerce/detail/:id' element={<ItemDetailContainer setCart={addProduct}/>}/>
          <Route path='/house_tech_project_ecommerce/categories/:id' element={<CategoryListContainer setCart={addProduct}/>}/>
        </Routes>
        {/* <header className="App-header">
          <h1>Welcome to Housetech Shop</h1>
          <ItemDetailContainer title="Item List"/>
        </header> */}
        
        
      
      </BrowserRouter>
    </div>
  )
}

export default App;
